import { useLocation, useNavigate } from 'react-router-dom';
import 'assets/css/rentalHistoryDetail.css';

function Detail() {
  const navigate = useNavigate();
  const location = useLocation();
  const row = location.state;
  const historyBack = () => {
    navigate(-1);
  };

  return (
    <div className="section__grid__wrap content rental__history__detail">
      {/* sub_nav */}
      <div className="sub__nav">
        <div className="left">
          <button onClick={historyBack}>
            <svg viewBox="0 0 24 24" stroke="white">
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth="2"
                d="M11 17l-5-5m0 0l5-5m-5 5h12"
              />
            </svg>
          </button>
          <dl>
            <dt className="f__medium">장애 내역</dt>
          </dl>
        </div>
      </div>
      {/* // sub_nav */}
      {/* 디바이스 ID */}
      <section className="id__wrap">
        <div className="section__inner">
          <div className="top">
            <p className="normal__title f__medium">디바이스ID</p>
          </div>
          <input defaultValue={row.deviceId} className="custom__input" readOnly />
        </div>
      </section>
      {/* // 디바이스 ID */}
      {/* Transaction ID */}
      <section className="id__wrap">
        <div className="section__inner">
          <div className="top">
            <p className="normal__title f__medium">Transaction ID</p>
          </div>
          <input type="text" defaultValue={row.transaction} className="custom__input" readOnly />
        </div>
      </section>
      {/* // Transaction ID */}
      {/* 디바이스명 */}
      <section className="device__name grid1">
        <div className="section__inner">
          <div className="top">
            <p className="normal__title f__medium">디바이스명</p>
          </div>
          <input type="text" defaultValue="을지로3가" className="custom__input" />
        </div>
      </section>
      {/* // 디바이스명 */}
      <section className="grid1 error__info">
        <div className="section__inner">
          <div className="top">
            <p className="normal__title f__medium">장애정보</p>
          </div>
          <div className="list__wrap">
            <ul>
              <li>
                <dl>
                  <dt>장애코드</dt>
                  <dd>
                    <input type="text" className="custom__input" defaultValue="ABC001" />
                  </dd>
                </dl>
              </li>
              <li>
                <dl>
                  <dt>발생화면</dt>
                  <dd>
                    <input type="text" className="custom__input" defaultValue="시작화면" />
                  </dd>
                </dl>
              </li>
            </ul>
            <dl>
              <dt>장애명칭</dt>
              <dd>
                <input type="text" className="custom__input" defaultValue="도어 센서 닫기 오류" />
              </dd>
            </dl>
            <dl>
              <dt>장애내용</dt>
              <dd>
                <textarea
                  className="custom__input"
                  defaultValue="배터리 반납 중 셔터 닫기 장애 발생"
                />
              </dd>
            </dl>
          </div>
        </div>
      </section>
      {/* 사용자 정보 */}
      <section className="user__certification grid1">
        <div className="section__inner">
          <div className="top">
            <p className="normal__title f__medium">사용자 정보</p>
          </div>
          <div className="list__wrap">
            <ul>
              <li>
                <dl>
                  <dt>사용자명</dt>
                  <dd>
                    <input type="text" className="custom__input" defaultValue="홍길동" />
                  </dd>
                </dl>
              </li>
              <li>
                <dl>
                  <dt>Platform</dt>
                  <dd>
                    <input type="text" className="custom__input" defaultValue="Android 15" />
                  </dd>
                </dl>
              </li>
              <li>
                <dl>
                  <dt>App버전</dt>
                  <dd>
                    <input type="text" className="custom__input" defaultValue="1.3.1" />
                  </dd>
                </dl>
              </li>
            </ul>
          </div>
        </div>
      </section>
      {/* // 사용자 정보 */}
      {/* 담당자 정보 */}
      <section className="error__info grid1">
        <div className="section__inner">
          <div className="top">
            <p className="normal__title f__medium">담당자 정보</p>
          </div>
          <div className="list__wrap">
            <ul>
              <li>
                <dl>
                  <dt>현장관리자</dt>
                  <dd>
                    <input type="text" className="custom__input" defaultValue="미배치" />
                  </dd>
                </dl>
              </li>
              <li>
                <dl>
                  <dt>조치일시</dt>
                  <dd>
                    <input
                      type="text"
                      className="custom__input"
                      defaultValue="2022-12-01 11:00:11"
                    />
                  </dd>
                </dl>
              </li>
            </ul>
            <dl>
              <dt>조치내역</dt>
              <dd>
                <textarea
                  className="custom__input"
                  defaultValue="배터리 반납 중 셔터 닫기 장애 발생"
                />
              </dd>
            </dl>
          </div>
        </div>
      </section>
      {/* // 담당자 정보 */}
    </div>
  );
}

export default Detail;
